		// },60000)
		// //每秒获取一次时间
		// timeInter = setInterval(()=>{
		// 	this.getTimes();
		// },1000)
		try {
		    const BookReadData = uni.getStorageSync('BookReadcontent');
			// console.log(BookReadData)
		    this.BookData = BookReadData;
			this.listData = BookReadData.chapter;
			this.GetBookInfo();
		} catch (e) {
			console.log(e)
			uni.showToast({
			    title: '数据异常',
			    icon: 'none'
			});
		}
	},
	methods:{
		GetBookInfo() {
			uni.setNavigationBarTitle({
			    title: this.BookData.title
			});
			
			//获取书籍信息
			this.GetReadContent();
			
		},
		GetReadContent() {
			let chapterid = '';
			this.currentchapter = this.BookData.firstchapter;
			// console.log(this.BookData)
			if(!this.BookData.ischoosechatper){
				chapterid = uni.getStorageSync('chapterid'+this.BookData.id)?uni.getStorageSync('chapterid'+this.BookData.id):this.currentchapter;
			}else{
				chapterid = this.currentchapter;
				
			}
			// console.log(chapterid)
			// console.log(uni.getStorageSync('chapterid'+this.BookData.id));
			uni.showLoading({title: '加载中...'});
			
			//请求章节内容
			uni.request({
				url:ApiUrl.ApiUrl + "/api/novel/chapter",
				data:{
					id:this.BookData.id,
					key:chapterid
				},
				method: 'POST',
				success: res => {
					// console.log(JSON.stringify(res));
					uni.hideLoading();
					this.section_title = res.data.title;
					this.content_text = res.data.content;
					this.shuming = this.BookData.title;
					this.nextchapterid = res.data.next.id;
					//记录阅读章节记录
					uni.setStorage({
						key:'chapterid'+ this.BookData.id,
						data:res.data.id
					})
					this.buttonshow = true;
				},
				fail(e) {
					uni.hideLoading();
					uni.showToast({ title: '网络异常', icon: 'none' });
					// reject(e);
				}
			})
		},
		readNext(){
			console.log(uni.getStorageSync('chapterid'));
			if(this.nextchapterid){
				uni.showLoading({title: '加载中...'});
				uni.request({
					url:ApiUrl.ApiUrl + "/api/novel/chapter",
					data:{
						id:this.BookData.id,
						key:this.nextchapterid
					},
					method: 'POST',
					success: res => {
						// console.log(JSON.stringify(res));
						uni.hideLoading();
						this.section_title = res.data.title;
						this.content_text = res.data.content;
						this.shuming = this.BookData.title;
						this.nextchapterid = res.data.next.id;
						//记录阅读章节记录
						uni.setStorage({
							key:'chapterid'+this.BookData.id,
							data:res.data.id
						})
						uni.pageScrollTo({
							scrollTop: 0,
							duration: 300
						});
						this.buttonshow = true;
					},
					fail(e) {
						uni.hideLoading();
						uni.showToast({ title: '网络异常', icon: 'none' });
						// reject(e);
					}
				})
			}else{
				uni.showToast({title:'等待作者更新',icon:'none'});
			}
		},
		chapterlist(){
			uni.navigateTo({
				url: '/pages/profile/chapterlist'
			})
		},
		//修改正文字体大小
		changeFontSize(e){
			this.size = e.detail.value;
			uni.setStorageSync('fontsize',e.detail.value);
		},
		//修改正文行间距
		changeLineHeight(e){
			this.lineHeight = e.detail.value;
			uni.setStorageSync('lineHeight',e.detail.value);
		},
		back(){
			console.log('返回')
			uni.navigateBack({});
		},
		dianjile(){
			this.show=!this.show;
		},
		//切换主题
		qiehuan(e){
			this.fontColor=zhuti.data[e].fontColor;//菜单字体颜色
			this.pageBg=zhuti.data[e].pageBg;//页面背景色
			this.menuBg=zhuti.data[e].menuBg;//菜单背景色
			this.textColor=zhuti.data[e].textColor;//富文本文字颜色
			uni.setStorageSync('zhuti',e);
			this.dqzhuti=e;
		},
